export const Prices = (props) => {
	return(
		<>
			<h2>Prices</h2>
			<small>{props.bpi.disclaimer}</small>
			<h3>Last updated @: {props.bpi.time.updated}</h3>
			<ul>
				{props.bpi.bpi.map((currency) => {
					return(
						<li>
							Bitcoin rate for {currency.description} = {currency.rate_float}{currency.code}
						</li>
					)
				})}
			</ul>

		</>
	)
}